import $ from 'jquery';
import 'materialize-css';
import Swiper from 'swiper';
require('fancybox')($);

import '../blocks/i18n/jquery.i18n';
import '../blocks/i18n/jquery.i18n.emitter.js';
import '../blocks/i18n/jquery.i18n.emitter.bidi.js';
import '../blocks/i18n/jquery.i18n.fallbacks.js';
import '../blocks/i18n/jquery.i18n.language.js';
import '../blocks/i18n/jquery.i18n.parser.js';
import '../blocks/i18n/jquery.i18n.messagestore.js';


$(() => {

	$(".button-collapse").sideNav();

	$('.modal-trigger').leanModal();

	Swiper('.team__carousel', {
		direction: 'horizontal',
		loop: true,
		slidesPerView: 5,
		breakpoints: {
				1200:	{
					slidesPerView: 4
				},
				1000:	{
					slidesPerView: 3
				},
				800:	{
					slidesPerView: 2
				},
				600:	{
					slidesPerView: 1
				}
			}
	});


	$('.fancybox').fancybox({
		padding: 0
	});

	$(document).on('scroll', () => {
		$(window).scrollTop() > 0 ? $('.header').addClass('fixed') : $('.header').removeClass('fixed');
	});


	// scroll links
	$(document).on('click', '.menu-top a, #nav-mobile ul a, .scroll-link', event => {
		event.preventDefault();
		const section = $(event.currentTarget).attr('href');
		$('html, body').animate({scrollTop: $(section).offset().top}, 800);
	});


	// i18n
	let lang = 'en';
	const i18n = $.i18n();
	$.i18n().debug = true;

	/*
	$.i18n()
			.load('/i18n/en.json')
			.done( () => {
				console.log($.i18n('Intro'));
			});*/
	/*.done( () => {
		$('[data-i18n]').i18n();
	});*/


	function doI18n(lang) {
		i18n.locale = lang;
		i18n.load(
			'i18n/'+lang+'.json', lang
		).done( () => {

			const elements = $('[data-i18n]');
			elements.each(function(index, value) {
				var messageKey = $(value).attr('data-i18n');
				var txt = $.i18n(messageKey);
				$(elements[index]).html(txt);
			});
		});
	}
	doI18n(lang);


	// language select
	$(document).on('click', '#lang-switcher a', (event) => {
		event.preventDefault();
		const link = $(event.currentTarget);
		lang = link.data('lang');
		doI18n(lang);

		$('#lang-switcher a').removeClass('active');
		link.addClass('active');
		$('.lang-switcher__img').attr('src', link.find('img').attr('src'));
	});

});



